@import url("https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,200;0,300;0,400;0,500;0,600;0,700;1,300;1,400;1,500;1,600&display=swap");

body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Poppins", sans-serif;
}
.menu {
  height: 10vh;
}
#menu-mobile {
  height: 1.2rem;
  width: 1.7rem;
  background-color: transparent;
  border: none;
  position: relative;
  padding: 0;
  margin: 16px;
}
#menu-mobile span {
  background-color: black;
  height: 0.2rem;
  width: 100%;
  position: absolute;
  left: 0;
  transition: all 0.3s linear;
}
#menu-mobile span:first-child {
  top: 0;
}
#menu-mobile span:nth-child(2) {
  top: 50%;
  margin-top: -0.1rem;
}
#menu-mobile span:last-child {
  bottom: 0;
}
.transition {
  margin-top: 30%;
  transform: rotate(135deg);
}
.transition2 {
  transform: scale(0);
}
.transition3 {
  margin-bottom: 30%;
  transform: rotate(-135deg);
}
.menu_open {
  position: absolute;
  padding: 0;
  margin: 0;
  list-style-type: none;
  width: 200px;
  left: -200px;
  transition: all 0.3s ease-in-out;
  border-radius: 0 10px 10px 0;
}
.menu_open li {
  padding: 1rem;
  border-bottom: 1px solid;
}
.menu_open li:last-child {
  border: none;
}
.menu_open li a {
  text-decoration: none;
  color: black;
}
.menu_open li a:visited {
  color: black;
}
.show {
  transform: translateX(200px);
  transition: all 0.3s ease-in-out;
}
.container {
  height: 90vh;
  width: 100%;
}
.container_user {
  width: 30%;
  height: 30%;
  background: black;
  border-radius: 50%;
  margin: auto;
}
.container_bubble {
  height: 40vw;
  width: 40vw;
  background-color: transparent;
  border: 1px solid black;
  border-radius: 50%;
  margin: 2rem auto;
  position: relative;
  display: flex;
}
.bubble {
  height: 5vw;
  width: 5vw;
  background-color: black;
  border-radius: 50%;
  position: absolute;
  top: 0;
  left: 0;
  transform: translate(-50%, -50%);
}
.bubble_comp_container {
  width: 100%;
  height: 100%;
  position: relative;
}
.bubble_comp {
  position: absolute;
  width: 1.5vw;
  height: 1.5vw;
  background-color: blue;
  border-radius: 50%;
  transform: translate(-50%, -50%) rotate(45deg);
  margin: auto;
  text-align: center;
}
